const D1Animate = {
    drawPath: ({ line, duration }) => {
        const totalLength = line.getTotalLength();
        const pathStr = line.attr('d');
        return new Promise(resolve => {
            Snap.animate(
                0,
                1,
                val => {
                    const subPath = Snap.path.getSubpath(pathStr, 0, val * totalLength);
                    line.attr({ d: subPath });
                },
                duration,
                undefined,
                resolve
            );
        });
    },
    onPath: ({ path, duration = 3000, element, local = '' }) => {
        const startPt = Snap.path.getPointAtLength(path, 0);
        return new Promise(resolve => {
            Snap.animate(
                0,
                1,
                val => {
                    const { x, y } = Snap.path.getPointAtLength(path, val * Snap.path.getTotalLength(path));
                    const transform = local + 't' + [x - startPt.x, y - startPt.y];
                    element.transform(transform);
                },
                duration,
                undefined,
                resolve
            );
        });
    },
    blink: ({ interval = 0.5, duration = 500, element, opacity = 1 }) => {
        element.attr({ pointerEvents: 'none' });
        return new Promise(resolve => {
            Snap.animate(
                0,
                1,
                val => {
                    element.attr({ opacity: val });
                },
                duration,
                val => {
                    if (val < interval) return (-(1 / interval) * val + 1) * opacity;
                    else return (1 / (1 - interval)) * (val - interval) * opacity;
                },
                resolve
            );
        });
    },
    flip: ({ front, duration }) => {
        const { cx } = front.getBBox();
        return new Promise(resolve => {
            Snap.animate(0, 1, val => front.transform(Snap.matrix(1 - val, 0, 0, 1, val * cx, 0)), duration, undefined, resolve);
        });
    },
    sliding: ({ element, dx, dy, hiddenTime, duration }) => {
        const { local } = element.transform();
        return new Promise(resolve => {
            Snap.animate(
                0,
                1,
                val => {
                    const opacity = val < hiddenTime ? 1 : -(1 / (1 - hiddenTime)) * val + 1 / (1 - hiddenTime);
                    element.attr({ opacity }).transform(local + 't' + [dx * val * val, dy * val * val]);
                },
                duration,
                undefined,
                () => {
                    element.remove();
                    resolve();
                }
            );
        });
    },
    rolling: ({ element, directionAngle, duration, multiple, revolutions, easing = window.mina.linear, dxy }) => {
        const { local } = element.transform();
        const radius = element.getBBox().w / 2;
        const radian = directionAngle * (Math.PI / 180);
        const [x, y] = dxy
            ? [dxy.x / revolutions / multiple, dxy.y / revolutions / multiple]
            : [radius * Math.cos(radian) * Math.PI, -radius * Math.sin(radian) * Math.PI];
        const sign = Math.cos(radian) < 0 ? -1 : 1;
        const diretion = Math.abs(Math.sin(radian)) === 1 ? -Math.sin(radian) : sign;

        return new Promise(resolve => {
            Snap.animate(
                0,
                1,
                val => {
                    const translate = local + 't' + [revolutions * x * multiple * val, revolutions * y * multiple * val];
                    const rotate = 'r' + diretion * val * (revolutions * 360);
                    const scale = 's' + (1 + (multiple - 1) * val);
                    element.transform(translate + rotate + scale);
                },
                duration,
                easing,
                resolve
            );
        });
    },
    shake: ({ el, rotate, time }) => {
        const { local } = el.transform();
        return new Promise(resolve => {
            Snap.animate(
                0,
                1,
                val => {
                    let a;
                    if (val < 0.25) a = 4 * val;
                    else if (val < 0.75) a = -4 * val + 2;
                    else a = 4 * val - 4;
                    el.transform(local + 'r' + rotate * a);
                },
                time,
                undefined,
                resolve
            );
        });
    }
};

const D1Drag = function ({ dragItem, draggingItem, startCallback, moveCallback, endCallback, effectSoundUrl }) {
    dragItem = dragItem.addClass('cp');
    draggingItem && draggingItem.attr({ opacity: '0', pointerEvents: 'none' });

    const originPoint = dragItem.getBBox();
    const dragStart = element => element.data('originTransform', element.transform().local);
    const dragMove = (elemenet, dx, dy) => {
        const origin = elemenet.data('originTransform') || '';
        const snapInvMatrix = elemenet.transform().diffMatrix.invert();
        dx = snapInvMatrix.x(dx, dy);
        dy = snapInvMatrix.y(dx, dy);
        elemenet.transform(origin + 't' + [dx, dy]);
    };
    const dragEnd = element => element.data('originTransform', element.transform().local);
    const effectSound = effectSoundUrl && loadSound(effectSoundUrl);

    const start = () => {
        dragStart(dragItem);
        if (draggingItem) {
            dragStart(draggingItem);
            setDragState(false);
        }
        if (startCallback) startCallback();
        if (effectSoundUrl) effectSound.play();
    };
    const move = (dx, dy) => {
        dragMove(dragItem, dx, dy);
        if (draggingItem) {
            dragMove(draggingItem, dx, dy);
            setDragState(false);
        }
        if (moveCallback) moveCallback();
    };
    const end = () => {
        dragEnd(dragItem);
        if (draggingItem) {
            dragEnd(draggingItem);
            setDragState(true);
        }
        if (endCallback) endCallback();
        if (effectSoundUrl) effectSound.stop();
    };

    const setDragState = bool => {
        dragItem.attr({ opacity: Number(bool) });
        draggingItem.attr({ opacity: Number(!bool) });
    };

    const animate = async ({ pathStr }) => {
        const { local } = dragItem.transform();
        dragItem.attr({ pointerEvents: 'none' });
        if (draggingItem) {
            D1Animate.onPath({ path: pathStr, duration: 300, element: dragItem, local });
            await D1Animate.onPath({ path: pathStr, duration: 300, element: draggingItem, local });
        } else {
            await D1Animate.onPath({ path: pathStr, duration: 300, element: dragItem, local });
        }
        dragItem.attr({ pointerEvents: 'auto' });
    };

    dragItem.moveAnimate = async point => {
        const { x, y } = dragItem.getBBox();
        const pathStr = 'M' + [x, y] + 'L' + [point.x, point.y];
        await animate({ pathStr });
    };
    dragItem.reset = async (dx = 0, dy = 0) => {
        const { x, y } = dragItem.getBBox();
        const pathStr = 'M' + [x, y] + 'L' + [originPoint.x + dx, originPoint.y + dy];
        await animate({ pathStr });
    };
    dragItem.moveAnimateWithScale = ({ dx, dy, scale, time = 1000, callback }) => {
        const { local } = dragItem.transform();
        return new Promise(resolve => {
            Snap.animate(
                0,
                1,
                val => {
                    const move = 't' + [val * dx, val * dy];
                    const _scale = 's' + (1 - ((scale.from - scale.to) / scale.from) * val);
                    dragItem.transform(local + move + _scale);
                },
                time,
                undefined,
                () => {
                    if (callback) callback();
                    resolve();
                }
            );
        });
    };
    dragItem.hidden = () => dragItem.attr({ opacity: '0', pointerEvents: 'none' });
    dragItem.drag(move, start, end);

    return dragItem;
};

const shuffle = array => {
    const copyArray = array.map(el => el);
    for (let i = copyArray.length - 1; i > 0; i--) {
        const j = Math.floor(Math.random() * (i + 1));
        const k = copyArray[i];
        copyArray[i] = copyArray[j];
        copyArray[j] = k;
    }
    return copyArray;
};

const randomNumber = (range, n) => {
    let array = [];
    for (let l = range[0]; l < range[1] + 1; l++) {
        array.push(l);
    }
    return shuffle(array).slice(0, n);
};


const makeFigure = ({ g, type, size = 100 }) => {
    let s;
    switch (type) {
        case 'circle':
            s = size * 0.5;
            return g.circle(s, s, s).toDefs();
        case 'rect':
            s = size;
            return g.rect(0, 0, s, s).toDefs();
        case 'triangle':
            s = size * 0.58;
            return g.polygon(s, 0, 0, s * 1.732, s * 2, s * 1.732).toDefs();
        default:
    }
    return g;
};

