// 애니메이션 + trace 모듈(낱자)
const DM000001 = async function ({ canvas, ...p }) {
    const g = canvas.g();
    const dropValue = [
        { xy: [140, 200], size: 180 },
        { xy: [400, 200], size: 180 },
        { xy: [650, 200], size: 180 }
    ];
    const type = shuffle(['rect', 'circle', 'triangle']);
    const shapeAsCount = shuffle([2, 2, 1]);

    const imageEl = [];
    shapeImage.forEach((el, i) => {
        const randomIndexArr = randomNumber([0, el.image.length - 1], shapeAsCount[i]);
        el.image.forEach((img, j) => {
            if (randomIndexArr.indexOf(j) !== -1) {
                imageEl.push(loadImage(img).data('type', el.type));
            }
        });
    });

    //drop 영역 그리기
    const dropElement = type.map((el, i) => {
        const { xy, size } = dropValue[i];
        const fig = makeFigure({ type: el, size: size })
            .use()
            .transform('t' + xy)
            .addClass('f14 s01')
            .attr({
                strokeDasharray: '10 10',
                strokeWidth: 3,
                strokeLinecap: 'round',
                strokeLinejoin: 'round'
            })
            .center()
            .appendTo(g)
            .data('type', el);        
        return fig;
    });
    const scale = 1.3;
    let ansCount = 0;
    // drag element 그리기
    const imgs = shuffle(imageEl);
    const items = imgs.map((el, z) => {
        let dropIndex = undefined;
        const useImg = el
            .use()
            .transform('t' + [150 + z * 100, 400])
            .center()
            .appendTo(g)
            .data('type', el.data('type'));
        const { width, height } = useImg.getBBox();
        const { totalMatrix } = useImg.transform();
        const originPoint = [totalMatrix.e, totalMatrix.f];
        const dragGroup = D1Drag({
            dragItem: useImg,
            startCallback: function () {
                // 크기 키우기
                const { local } = useImg.transform();
                useImg.transform(local + 's' + scale);
                console.log('시작', useImg.transform());
            },
            moveCallback: function () {
                // 크기 키우기
                const { local, totalMatrix } = useImg.transform();
                useImg.transform(local + 's' + scale);
                // drop 영역에 진입 시 색상 변경
                const [cx, cy] = [totalMatrix.e + width / 2, totalMatrix.f + height / 2];
                dropElement.forEach((dropEl, i) => {
                    const bbox = dropEl.getBBox();
                    if (Snap.path.isPointInsideBBox(bbox, cx, cy)) {
                        dropEl.removeClass('f14').addClass('f06');
                        console.log(i);
                        dropIndex = i;
                    } else if (dropEl.attr('class').indexOf('f06') !== -1) {
                        dropEl.removeClass('f06').addClass('f14');
                        dropIndex = undefined;
                    }
                });
            },
            endCallback: async function () {
                // drop 영역 색상 초기화
                dropElement.forEach(dropEl => {
                    if (dropEl.attr('class').indexOf('f06') !== -1) dropEl.removeClass('f06').addClass('f14');
                });
                let bool = false; //정오답 판단
                if (!isNaN(dropIndex) && dropElement[dropIndex].data('type') === dragGroup.data('type')) (bool = true), ansCount++; // 인덱스가 있으면서 정답인 것
                const { e, f } = useImg.transform().totalMatrix;
                const xy = bool
                    ? [0, 0]
                    : [(originPoint[0] - ((scale - 1) * width) / 2 - e) / scale, (originPoint[1] - ((scale - 1) * height) / 2 - f) / scale]; //원점기준 gap : (scale-1)*width/2, (scale-1)*height/2
                dragGroup.attr('pointerEvents', 'none');
                let interval;
                console.log(useImg);
                if (!bool) {
                    interval = setInterval(() => {
                        const clone = useImg.clone();
                        clone.after(useImg);
                        clone.animate({ opacity: 0 }, 1000, undefined, () => {
                            clone.remove();
                        });
                    }, 300);
                }
                await dragGroup.moveAnimateWithScale({
                    dx: xy[0],
                    dy: xy[1],
                    scale: { from: scale, to: 1 },
                    callback: bool
                        ? undefined
                        : () => {
                              clearInterval(interval);
                          }
                }); // 사이즈 줄면서 해당 위치로 (정답시 그대로, 오답시 초기위치)
                // console.log('끝',el.transform());
                if (!bool) dragGroup.attr('pointerEvents', 'auto');
                if (ansCount > imgs.length - 1) {
                    dropElement.forEach(dropEl => dropEl.attr('opacity', 0));
                    await Promise.all(items.map(el => D1Animate.shake({ el: el, rotate: 20, time: 1000 })));
                    console.warn('완전 종료');
                }
            }
        }).attr('pointerEvents', 'none');
        return dragGroup;
    });

    const start = () => {
        items.forEach(async function (el) {
            await D1Animate.shake({ el: el, rotate: 20, time: 1000 });
            el.attr('pointerEvents', 'auto');
        });
    };

    start();
};

export default DM000001;
